import React from "react";

// Components
import ArticleCard from "../ArticleCard";
import ViewMoreLink from "../ViewMoreLink";
import AdsContainer from "../AdsContainer";

import "./styles.scss";

const FeaturedBlogs = ({ data, hasAds, tag, title }) => {
    if (data.length === 0) {
        return null;
    }
    let cards = data;
    if (hasAds) {
        const middle = parseInt(data.length / 2);
        cards = data.slice(0, middle)
            .concat({ node: { isAd: true } })
            .concat(data.slice(middle));
    }

    return (
        <section className="features">
            {title &&
                <h2 className="features-title">{title}</h2>
            }
            <ul className="features-list">
                {cards.map(({ node }) => {
                    if (node.isAd) {
                        return <li key="ads"><AdsContainer type={"billboard"} /></li>;
                    }
                    return (<li key={node.id}>
                        <ArticleCard data={node} />
                    </li>)
                })}
            </ul>
            <ViewMoreLink to={`/${tag}`} />
        </section>
    );
};

export default FeaturedBlogs;

