import React, { useEffect, useState } from "react";
import { StaticQuery, graphql } from 'gatsby';
import ReactGA from 'react-ga';
// Components
import FeaturedBlogs from "../components/FeaturedBlogs";
import Layout from "../components/layout";
import SEO from "../components/SEO";
import AdsContainer from "../components/AdsContainer";
import Headlines from "../components/Headlines";

import { createBlogPageMetadata } from "../jsonLd";

import "./styles.scss";

export function View({ data }) {
    return (
        <Layout>
            <>
                <SEO
                    title="TechWaifu - Most Recent Blog Posts"
                    metadata={[createBlogPageMetadata(data.recent.edges)]}
                />
                <section className="Homepage">
                    <h1 className="HomePageTitle">Most Recent Blogs</h1>
                    <Headlines data={data.recent.edges.slice(0, 5)} />
                    <FeaturedBlogs hasAds={true} data={data.recent.edges.slice(5)} title="The Latest" tag="blogs/all" />
                    <AdsContainer type={"leaderboard"} />
                </section>
            </>
        </Layout>
    )
}

export default function Template() {
  const [start] = useState(new Date());
  useEffect(() => {
    return () => {
      const cur = new Date();
      const diff = cur.getMilliseconds() - start.getMilliseconds();
      ReactGA.event({
        category: 'TimeOnHome',
        variable: 'time',
        value: diff, // in milliseconds
        label: "Home",
      });
    };
  });

  return (
    <StaticQuery
      query={graphql`
              query MyQuery {
                  recent: allMarkdownRemark(
                    limit: 15,
                    sort: { order: DESC, fields: [frontmatter___date] }
                  ) {
                    edges {
                        node {
                          id
                          excerpt
                          frontmatter {
                              author
                              slug
                              title
                              date(formatString: "MMMM D, YYYY")
                              modified_date(formatString: "MMMM D, YYYY")
                              cover_photo
                          }
                      }
                    }
                  }
              }
          ` }
      render={(data) => {
        return (<View data={data} />);
      }} />
  )
}
